}
#slideshow {
  position: relative;
  margin: 0px auto;
  width: 740px;
  height: 740px;
  box-shadow: 0 0 20px rgba(0,0,0,0.4);
}
#slideshow img {
  width: 700px;
  height: 700px;
}
#slideshow > div {
  position: absolute;
  top: 20px;
  left: 10px;
  right: 10px;
  bottom: 10px;
}
.apps {
  top: 100px;
  position: relative;
}
.showcase {
  border-top: solid 12px #ccc;
  border-bottom: solid 12px #ccc;
  background: #05181e;
  color: #fff;
  margin-bottom: 80px;
  font-family: "Lora";
  text-align: center;
}
.showcase h2 {
  font-family: "Satisfy";
  font-size: 5em;
}
.showcase .showcase-text {
  padding: 3rem;
}
.showcase .showcase-img {
  min-height: 30rem;
  background-size: cover;
}
.showcase ul {
  list-style: none;
}
.showcase b {
  font-size: 30px;
}
.border-bottom {
  border-bottom: solid 12px #ccc;
}
.border-top {
  border-top: solid 12px #ccc;
}
.page-section {
  margin-top: 5rem;
  margin-bottom: 5rem;
}
.section-heading .section-heading-upper {
  display: block;
  font-size: 1rem;
  font-weight: 800;
}
.section-heading .section-heading-lower {
  display: block;
  font-size: 5rem;
  font-weight: 100;
  font-family: 'Satisfy';
}
.bg-faded {
  background-color: rgba(255,255,255,0.85);
}
.intro {
  position: relative;
  color: #000;
}
@media (min-width: 794px) {
  .intro .intro-img {
    width: 100%;
  }
}
@media (min-width: 636px) {
  .intro .intro-img {
    width: 100%;
  }
}
@media (min-width: 992px) {
  .intro .intro-img {
    width: 75%;
    float: right;
  }
  .intro .intro-text {
    left: 0;
    width: 60%;
    margin-top: -32rem;
    position: absolute;
    font-family: 'Lora';
    padding-bottom: 40px;
    z-index: 99;
  }
  .intro .intro-text .intro-button {
    width: 100%;
    left: 0;
    position: absolute;
    bottom: -2rem;
  }
}
.intro p {
  color: #000;
}
@media (max-width: 794px) {
  .intro p {
    font-size: 2rem;
  }
}
@media (min-width: 1200px) {
  .intro .intro-text {
    width: 45%;
  }
}
.btn-xl {
  font-weight: 400;
  font-size: 1.8rem;
  padding-top: 1.5rem;
  padding-bottom: 1.5rem;
  padding-left: 2rem;
  padding-right: 2rem;
}
.rounded {
  border-radius: 0.75rem !important;
}
.shadow {
  box-shadow: 6px 6px 10px #222;
}
.bg-primary {
  padding: 8rem 0;
  background-color: #b53730 !important;
  font-size: 2rem;
  text-align: center;
  width: 100%;
}
.bg-primary h2 {
  font-size: 40px;
  color: #000;
  font-family: "Satisfy";
}
.bg-primary p {
  font-family: "Lora";
}
.bg-primary h2,
p {
  color: #fff;
  display: block;
  margin: 0 auto;
}
ul {
  padding: 6px 0px 0px 0px;
}
.lead {
  margin-bottom: 0px;
}
.mx-auto {
  width: 100%;
  float: left;
  margin-top: -8px;
}
.dad {
  border: solid 8px #ccc;
  border-radius: 10px;
}
@-webkit-keyframes card {
  0% {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
  50% {
    -webkit-transform: translate3d(0, -135px, 0);
            transform: translate3d(0, -135px, 0);
  }
  51% {
    z-index: 10;
    -webkit-transform: translate3d(0, -135px, 0);
            transform: translate3d(0, -135px, 0);
  }
  100% {
    z-index: 10;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
}
@keyframes card {
  0% {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
  50% {
    -webkit-transform: translate3d(0, -135px, 0);
            transform: translate3d(0, -135px, 0);
  }
  51% {
    z-index: 10;
    -webkit-transform: translate3d(0, -135px, 0);
            transform: translate3d(0, -135px, 0);
  }
  100% {
    z-index: 10;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
}
